@import "./utils/mixins.scss";

.container {
  max-width: 1800px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;

  .imgContainer {
    display: flex;
    flex-direction: column;
    align-items: center;

    .carIMG {
      height: 430px;
    }

    .carName {
      @include font(35px, 500, #161616);
    }
  }

  .detailsContainer {

    .details {
      margin: 30px 30px 0 0;
  
      .detailsTitle {
        @include font(20px, 500, #161616);
      }
  
      .item {
        @include font(15px, 500, #2c2c2c);
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        width: 350px;
        padding: 5px;

        &:nth-child(even) {
          background-color: rgb(241, 241, 241);
        }

        div {
          @include font(15px, 500, #666666);
        }
      }

      .itemPrice {
        @include font(19px, 500, #000000);
        display: flex;
        align-items: center;
        margin: 20px 0;
        padding: 5px;

        div {
          padding: 10px;
          background-color: rgb(90, 173, 51);
          color: white;
          border-radius: 5px;
          margin-left: 10px;
        }

      }
  
    }
  }
}

@media screen and (max-width: 1000px) {
  .container {
    .imgContainer {
      .carIMG {
        height: 280px;
      }

      .carName {
        @include font(25px, 500, #161616);
      }
    }
  
    .detailsContainer {
      .details {
        margin: 25px 30px 0 0;
    
        .detailsTitle {
          @include font(17px, 500, #161616);
        }
    
        .item {
          @include font(13px, 500, #2c2c2c);
          width: 280px;

          div {
            @include font(12px, 500, #666666);
          }
        }
  
        .itemPrice {
          @include font(17px, 500, #000000);
  
          div {
            padding: 8px;
          }
  
        }
      }
    }
  }
}


@media screen and (max-width: 700px) {
  .container {
    flex-direction: column;
  
    .detailsContainer {
      .details {
        display: flex;
        flex-direction: column;
        align-items: center;
        .detailsTitle {
          @include font(20px, 500, #161616);
        }
    
        .item {
          @include font(15px, 500, #2c2c2c);
          width: 350px;
  
          div {
            @include font(15px, 500, #666666);
          }
        }
  
        .itemPrice {
          @include font(19px, 500, #000000);
  
          div {
            padding: 10px;
          }
        }
      }
    }
  }
}